<template>
	<div id="login">
		<div class="content">
			<div class="header">
				<div class="logo">
					<img v-if="config.logo" :src="config.logo" alt=""/>
					<img v-else src="static/img/head.png" alt=""/>
				</div>
				<div class="name">{{config.title}}</div>
			</div>
			<div class="list-block">
				<van-cell-group>
					<van-field
						v-model="mobile"
						left-icon="phone1"
						type="number"
						placeholder="请输入手机号"
					/>
					<van-field
						v-model="password"
						left-icon="lock"
						type="password"
						placeholder="请输入密码"
					/>
				</van-cell-group>
				<router-link :to="util.getUrl({path: '/pages/auth/forget'})" class="list-password">忘记密码<van-icon name='right'></van-icon></router-link>
				<div class="content-padded">
					<div @click="onLogin" class="button-login">登陆</div>
				</div>
			</div>
			<div class="text">
				<p>还没有账号？<router-link :to="util.getUrl({path: '/pages/auth/register'})" class="text-a">立即注册</router-link></p>
			</div>
		</div>
	</div>
</template>

<script>
	import util from '@/config/util'
	export default {
		data() {
			return {
				mobile: '',
				password: '',
				config: {},
			}
		},
		beforeRouteEnter (to, from, next){
			if(from.name) {
				window.redirct_url = {path: from.path, query: from.query};
			}
			next();
		},
		methods: {
			onLogin() {
				if(!this.mobile) {
					this.$toast('手机号不能为空');
				}
				var reg = /^[01][3456789][0-9]{9}/;
				if(!reg.test(this.mobile)) {
					this.$toast('手机号格式错误');
					return false;
				}
				if(!this.password) {
					this.$toast('请输入密码');
				}
				this.util.request({
					url: 'wmall/auth/login',
					method: 'POST',
					data: {
						mobile: this.mobile,
						password: this.password
					},
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message, '', 1000);
						return false;
					}
					result = result.message;
					this.util.setStorage('itoken', result.member.token);

					var redirct_url = window.redirct_url;
					if(!redirct_url || (redirct_url && redirct_url.path.indexOf('pages/auth/login') != -1)) {
						redirct_url = {path: '/pages/home/index'};
					}
					this.$router.replace(util.getUrl(redirct_url));
				})
			},
			onLoad() {
				this.util.request({
					url: 'wmall/auth/login',
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message, '', 1000);
						return false;
					}
					this.config = result.message.config_mall;
				})
			}
		},
		mounted() {
			this.$nextTick(() => {});
			this.onLoad();
		}
	}
</script>

<style>
	#login .content{
		position: fixed;
		top: 0px;
		right: 0;
		left: 0;
		bottom: 0px;
		width: 100%;
		background: #fff;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#login .content::-webkit-scrollbar {
		display: none;
	}
	#login .content .header{
		background: url(../../../static/img/bg-mypage.png) no-repeat 0 0;
		background-size: 100% 100%;
		overflow: hidden;
	}
	#login .content .header .logo{
		position: relative;
		height: 88px;
		width: 88px;
		border-radius: 100%;
		margin: 20px auto 0;
	}
	#login .content .header .logo img{
		width: 100%;
		height: 100%;
		border-radius: 100%;
	}
	#login .content .header .logo:before{
		position: absolute;
		content: "";
		height: 94px;
		width: 94px;
		border: 1px solid #fff;
		border-radius: 94px;
		top: -4px;
		left: -4px;
	}
	#login .content .header .name{
		height: 50px;
		color: #fff;
		text-align: center;
		font-size: 20px;
		line-height: 50px;
	}
	#login .content .list-block{
		position: relative;
	}
	#login .content .list-block .van-cell .van-icon{
		font-size: 16px;
		color: #999;
	}
	#login .content .list-block .van-cell .van-field__body{
		font-size: 17px;
	}
	#login .content .list-block .content-padded{
		margin: 20px 20px 10px 20px;
	}
	#login .content .list-block .content-padded .button-login{
		position: relative;
		display: block;
		height: 40px;
		line-height: 40px;
		color: #fff;
		background-color: #f6383a;
		border-radius: 25px;
		text-align: center;
		text-decoration: none;
		text-overflow: ellipsis;
		white-space: nowrap;
		cursor: pointer;
	}
	#login .content .list-block .list-password{
		z-index: 10000;
		position: absolute;
		top: 58px;
		right: 15px;
		font-size: 14px;
		color: #888;
	}
	#login .content .text{
		padding: 0 32px;
		color: #888;
		font-size: 16px;
		text-align: center;
	}
	#login .content .text p{
		padding: 0;
		margin: 0;
		line-height: 24px;
	}
	#login .content .text p .text-a{
		color: #0894ec;
	}
</style>



